        {prop: 'lastYearComInsCount', label: '商业险上年出险次数', width: 220, align: 'center'},
        {prop: 'lastYearComInsAmount', label: '商业险上年出险金额', width: 250, align: 'center'},
        {prop: 'age', label: '年龄', width: 120, align: 'center'},
        {prop: 'comAuto', label: '商业自主系数', width: 120, align: 'center'},
        {prop: 'marketRate', label: '车险市场费用率', width: 130, align: 'center'}
      ],
      bizSmallTypeData: [
        {
          lastYearComInsAmount: '5000-10000',
          lastYearComInsCount: '3次及以上',
          age: '20-30',
          comAuto: 0.49,
          marketRate: '39%'
        }
      ],
      paramConfigColumn: [
        {prop: 'isInverseDiscount', label: '是否反算折扣', width: 120, align: 'center'},
        {prop: 'isInverseCost', label: '是否反算费用', width: 120, align: 'center'},
        {prop: 'targetCost', label: '配置目标成本(%)', width: 140, align: 'center'},
        {prop: 'nonMarketRate', label: '配置非市场费用率(%)', width: 170, align: 'center'},
        {prop: 'comAutoUp', label: '商业险自主系数上限', width: 100, align: 'center'},
        {prop: 'comAutoDown', label: '商业险自主系数下限', width: 100, align: 'center'},
        {prop: 'comMarketRateUp', label: '商业险市场费用率上限(%)', width: 120, align: 'center'},
        {prop: 'comMarketRateDown', label: '商业险市场费用率下限(%)', width: 120, align: 'center'},
        {prop: 'forceMarketRate', label: '交强险市场费用率配置值', width: 120, align: 'center'},
        {prop: 'expComPercent', label: '预期商业险保费占比', width: 110, align: 'center'},
        {prop: 'expForcePercent', label: '预期交强险保费占比', width: 110, align: 'center'},
        {prop: 'expPremiumScale', label: '预期该类业务保费规模(万元)', width: 130, align: 'center'},
        {prop: 'expPremiumPercent', label: '预期该类业务保费占比', width: 110, align: 'center'}
      ],
      paramConfigData: [
        {
          isInverseDiscount: '否',
          isInverseCost: '否',
          targetCost: 100.00,
          nonMarketRate: 20.00,
          comAutoUp: 1.3225,
          comAutoDown: 1.3225,
          comMarketRateUp: '30',
          comMarketRateDown: '15',
          forceMarketRate: '0.4',
          expComPercent: 75.00,
          expForcePercent: 25.00,
          expPremiumScale: 800,
          expPremiumPercent: 10.00
        }
      ],
      actualPremiumPercentColumn: [
        {prop: 'comAutoAvg', label: '商业险自主系数平均值', width: 110, align: 'center'},
        {prop: 'comMarketRateAvg', label: '商业险市场费用率平均值', width: 120, align: 'center'},
        {prop: 'comMarketRateUpPercent', label: '商业险市场费用率上限占比', width: 120, align: 'center'},
        {prop: 'comMarketRateDownPercent', label: '商业险市场费用率下限占比', width: 120, align: 'center'},
        {prop: 'comFullRate', label: '商业险满期赔付率(含IBNR)', width: 120, align: 'center'},
        {prop: 'comExpCostRate', label: '商业险预期经营成本率(%)', width: 120, align: 'center'},
        {prop: 'forceFinalRate', label: '交强险终极赔付率(%)', width: 120, align: 'center'},
        {prop: 'forceFullRate', label: '交强险满期赔付率(%)(含IBNR)', width: 140, align: 'center'},
        {prop: 'forceExpCostRate', label: '交强险预期经营成本率(%)', width: 120, align: 'center'},
        {prop: 'actualComPercent', label: '实际商业险保费占比', width: 110, align: 'center'},
        {prop: 'actualForcePercent', label: '实际交强险保费占比', width: 110, align: 'center'},
        {prop: 'actualPremiumPercent', label: '实际该类业务保费占比', width: 110, align: 'center'}
      ],
      actualPremiumPercentData: [
        {
          comAutoAvg: 0.700,
          comMarketRateAvg: 36.00,
          comMarketRateUpPercent: '19.6',
          comMarketRateDownPercent: '2.1',
          comFullRate: 66.83,
          comExpCostRate: 97.9,
          forceFinalRate: 121.83,
          forceFullRate: 122.1,
          forceExpCostRate: 57.9,
          actualComPercent: 80.00,
          actualForcePercent: 20.00,
          actualPremiumPercent: 12.00
        }
      ],
      futureColumn: [
        {prop: 'totalFinalRate', label: '车险合计终极赔付率(%)', width: 120, align: 'center'},
        {prop: 'totalFullRate', label: '车险合计满期赔付率(%)(含IBNR)', width: 140, align: 'center'},
        {prop: 'totalMarketRate', label: '车险合计市场费用率(%)', width: 120, align: 'center'},
        {prop: 'totalExpCostRate', label: '车险合计预期经营成本率(%)', width: 120, align: 'center'}
      ],
      futureData: [
        {
          totalFinalRate: 34.766,
          totalFullRate: 77.844,
          totalMarketRate: '35.6',
          totalExpCostRate: '88.9'
        }
      ],
      selectDialogArr: ['isInverseDiscount', 'isInverseCost'],
      inputDialogArr: [
        'comAutoUp',
        'comMarketRateUp',
        'forceMarketRate',
        'expComPercent',
        'expForcePercent',
        'expPremiumScale',
        'expPremiumPercent'
      ],
      columnShowList: [
        '智能推荐结果',
        '参数配置',
        '实际该类业务保费占比',
        '车险未来经营情况预测'
      ],
      autoDownEdit2: false,
      marketEdit2: false
    }
  },
  computed: {
    autoDownEdit () {
      const result = []
      for (let i = 0; i < this.data.length; i++) {
        result.push(false)
      }
      return result
    },
    marketEdit () {
      const result = []
      for (let i = 0; i < this.data.length; i++) {
        result.push(false)
      }
      return result
    },
    ...mapState({
      sectors: state => state.individual.sectors,
      tableHeader: state => state.app.tableHeader
    })
  },
  methods: {
    supply () {
      this.$router.push({path: '/admin/individual/declare/supply'})
    },
    sectorClick (id) {
      console.log(id)
    },
    toggleColumn (cols, status) {
      this.columns.forEach(item => {
        if (cols.includes(item.firstTitle)) {
          item.isShow = status
        }
      })
    },
    toggleColumnShow (vals) {
      const all = ['智能推荐结果', '参数配置', '实际该类业务保费占比', '车险未来经营情况预测']
      let show = []
      let hide = []
      if (vals.length === 4) {
        show = all
      } else if (vals.length === 0) {
        hide = all
      } else {
        all.forEach(val => {
          if (vals.includes(val)) {
            show.push(val)
          } else {
            hide.push(val)
          }
        })
      }
      this.toggleColumn(show, true)
      this.toggleColumn(hide, false)
    },
    disCountChange (value, rowIndex) {
      if (value === '是') {
        this.autoDownEdit[rowIndex] = true
      } else {
        this.autoDownEdit[rowIndex] = false
      }
    },
    costChange (value, rowIndex) {
      if (value === '是') {
        this.marketEdit[rowIndex] = true
      } else {
        this.marketEdit[rowIndex] = false
      }
    },
    disCountChange2 (value) {
      if (value === '是') {
        this.autoDownEdit2 = true
      } else {
        this.autoDownEdit2 = false
      }
    },
    costChange2 (value) {
      if (value === '是') {
        this.marketEdit2 = true
      } else {
        this.marketEdit2 = false
      }
    }
  }
}
</script>

<style scoped>
  .btn > .el-checkbox-group {
    float: left;
    margin-right: 20px;
  }
  .btn .el-checkbox-group{
    margin:10px 8px;
  }
</style>



